.about-main {
    max-width: 1700px;
    margin: 0 auto;
    width: 100%;
    display: flex;
    margin-bottom: 162px;
}
.about {
		&__container {
            width: 85%;
            background: #1E0C06;
            filter: drop-shadow(0px 4px 52px rgba(245, 245, 245, 0.17));
            border-radius: 0px 42px 42px 0px;
            display: flex;
            justify-content: end;
            gap: 60px;
            padding: 30px 40px;
            opacity: 0.8;
            @media (max-width:1024px) {
                flex-direction: column;
                align-items: center;
                width: 95%;
            }
		}
		&__container-item {
            display: flex;
            flex-direction: column;
            h2{
                font-style: normal;
                font-weight: 500;
                font-size: 90px;
                line-height: 132.9%;
                text-transform: uppercase;
                color: #F5F5F5;
                margin-bottom: 62px;
                
            }
            p{
                max-width: 530px;
                font-family: 'Inter';
                font-style: normal;
                font-weight: 700;
                font-size: 30px;
                line-height: 139.4%;
                color: #b3b1b1;
                span{
                    color: #F5F5F5;
                }
            }
            img{
                max-width: 425px;
                width: 100%;
            }
            @media (max-width:425px) {
                p{
                    font-size: 16px;
                }
                h2{
                    font-size: 40px;
                    margin-bottom: 30px;
                }
            }
		}
}
